import React, { useState } from "react";
import './AddedItems.css';
import closeItemIcon from '../icons/cancel.png';
import Discount from "../Discount/Discount";

export default function AddedItems(props) {
    const {item, price, setProductBeingRemovedPrice} = props;
    const [isOverImg, setIsOverImg] = useState(false);
    const [isOverDeleteBtn, setIsOverDeleteBtn] = useState(false);

    const onImgMouseOver = () => setIsOverImg(true);
    const onImgMouseOut = () => setIsOverImg(false);

    const onDeleteBtnOver = e => {
      setIsOverDeleteBtn(true);
      setProductBeingRemovedPrice(price);
    };

    const onDeleteBtnOut = () => {
      setIsOverDeleteBtn(false);
      setProductBeingRemovedPrice(0);
    };

    const onDeleteBtnClicked = e => {
      const parent = e.currentTarget.parentElement;
      parent.style.transform = 'translateX(-150%)';

      setTimeout(() => props.onDeleteItemBtnClicked(e, item.id), 480);
    }
  
    const transform = `scale(${isOverImg ? 0.75 : 1})`;
    const textDecoration = isOverDeleteBtn ? 'line-through' : 'none';

    return <div> 
    <div className='shopping-cart-list-item-wrapper'>
    <div className='shopping-cart-border'></div>
      <button onMouseOver={onDeleteBtnOver} onMouseOut={onDeleteBtnOut}
      className='shopping-cart-delete-btn' onClick={ onDeleteBtnClicked }>
        <img src={closeItemIcon} alt='delete' className='delete-item-icon' />
      </button>
      <li onMouseOver={onImgMouseOver} onMouseOut={onImgMouseOut} className="shopping-cart-list-item-img-over-container"></li>
      <li><img src={item.src} style={{transform: transform}} className='added-item-img' alt={item.name} /></li>
      <li style={{textDecoration: textDecoration}} className='shopping-cart-list-item shopping-cart-item-name'>{item.isPizza ? item.name + ' ' + item.size : item.name}</li>
      <li style={{textDecoration: textDecoration}} className='shopping-cart-list-item shopping-cart-item-count'>x{item.totalCount}</li>
      <li style={{textDecoration: textDecoration}} className='shopping-cart-list-item shopping-cart-total-price'>Total price: <span className='total-price-content'>${price.replace('.',',')}</span>{item.onSale && <Discount/>}</li>
    </div>
    </div>
}