import React from "react";
import { connect } from "react-redux";
import { toggleMobileNavigationAC } from "../header-reducer/header-reducer";
import './Navbar.css';

function Navbar(props) {
  const onNavbarContainerClicked = e => {
    Array.from(e.currentTarget.children)
    .forEach(child => child.style.background = !props.mobNavbarIsClicked ? 'bisque' : 'aliceblue');

    props.toggleMobileNavigation(!props.mobNavbarIsClicked);
  }

  const onNavbarContainerOver = e => {
    if (props.mobNavbarIsClicked) return;
    Array.from(e.currentTarget.children)
    .forEach(child => child.style.background = '#c9dbeb');
  };

  const onNavbarContainerOut = e => {
    if (props.mobNavbarIsClicked) return;
    Array.from(e.currentTarget.children)
    .forEach(child => child.style.background = 'aliceblue')
  };

    return (
      <nav>
        <ul className='navbar-mob-container'>
          <div className='navbar-container' onMouseOver={onNavbarContainerOver}
          onMouseOut={onNavbarContainerOut} onClick={onNavbarContainerClicked}>
            <li className='first'></li>
            <li className='second'></li>
            <li className='third'></li>
          </div>
        </ul>
        {props.mobNavbarIsClicked &&
          <ul className='mob-navigation-container'>
            <li className='mob-navbar-item'><a className='navbar-item-link' href='#root'>Home</a></li>
            <li className='mob-navbar-item'><a className='navbar-item-link' href='#menu'>Menu</a></li>
            <li className='mob-navbar-item'><a className='navbar-item-link' href='#contact'>Contact</a></li>
          </ul>
        }
          <ul className='navigation-container'>
            <li className='navbar-item'><a className='navbar-item-link' href='#root'>Home</a></li>
            <li className='navbar-item'><a className='navbar-item-link' href='#menu'>Menu</a></li>
            <li className='navbar-item'><a className='navbar-item-link' href='#contact'>Contact</a></li>
          </ul>
      </nav>
    )
  }

  class NavbarContainer extends React.Component {
    render() {
      return <Navbar {...this.props} />
    }
  }

  const mstp = state => {
    return {
      mobNavbarIsClicked: state.header.nav.mobNavbarIsClicked,
    }
  }

  export default connect(mstp, {
    toggleMobileNavigation: toggleMobileNavigationAC,
  })(NavbarContainer);