import React, { useState } from "react";
import { connect } from "react-redux";
import { toggleAddNewItemNotificationAC, toggleShoppingCartDisplayAC } from "../header-reducer/header-reducer";
import { clearShoppingCartAC, deleteItemFromShoppingCartAC } from "../menu-reducer/menu-reducer";
import { blurPageAC } from "../page-reducer/page-reducer";
import swal from 'sweetalert';
import closeIcon from '../icons/close.png';
import clearIcon from '../icons/eraser.png';
import buyIcon from '../icons/payment.png';
import emptyCart from '../icons/empty-cart.png';
import './ShoppingCart.css';
import AddedItems from "../AddedItems/AddedItems";

function ShoppingCart(props) {
    const [productBeingRemovedPrice, setProductBeingRemovedPrice] = useState(0);

    const {deleteItemFromShoppingCart, toggleAddNewItemNotification} = props;

    const onDeleteItemBtnClicked = (e, id) => {
      deleteItemFromShoppingCart(id);
      toggleAddNewItemNotification(false);
      setProductBeingRemovedPrice(0);
    }

    if (!props.shoppingCartIsOpen) return null;
    const getItemPrice = item => (item.price * item.totalCount).toFixed(2);
    let totalItemsPrice = 0, totalItemsCount = 0;
    

    const onEmptyCartIconLoad = e => {
      e.target.classList.add('empty-cart-icon-animation');
      setTimeout(() => e.target.classList.remove('empty-cart-icon-animation'), 2100);
    };

    return !props.addedItems.length ? <div className='shopping-cart-wrapper' id='shopping-cart'>
      <p className='empty-shopping-cart-text'>
      <img src={emptyCart} alt="cart" onLoad={onEmptyCartIconLoad} className="empty-cart-icon" />
      <br />
        Your shopping cart is empty
        </p>
      <div className='shopping-cart-btns-wrapper'>
        <button className='close-shopping-cart-btn shopping-cart-btn' onClick={props.onCloseBtnClicked}>
          <img src={closeIcon} alt='close' className='shopping-cart-btn-icon' />
            Close
          </button>
        </div>
    </div> : (
      <div className='shopping-cart-wrapper' id='shopping-cart'>
        <ul className='shopping-cart-list'>

          {props.addedItems.map(item => {
            const price = getItemPrice(item);
            totalItemsPrice += +price;
            totalItemsCount += item.totalCount;
            return <AddedItems setProductBeingRemovedPrice={setProductBeingRemovedPrice}
            key={item.id} onDeleteItemBtnClicked={onDeleteItemBtnClicked} 
            item={item} price={price} />
          })}

        </ul>
        <p className='total-items-price'>Total: ${
          productBeingRemovedPrice ? (totalItemsPrice - productBeingRemovedPrice).toFixed(2).replace('.',',') :
          totalItemsPrice.toFixed(2).replace('.',',')
        }</p>
        <div className='shopping-cart-btns-wrapper'>
        <button className='close-shopping-cart-btn shopping-cart-btn' onClick={props.onCloseBtnClicked}>
          <img src={closeIcon} alt='close' className='shopping-cart-btn-icon' />
            Close
          </button>
          <button className='clear-shopping-cart-btn shopping-cart-btn' onClick={props.onClearBtnClicked}>
          <img src={clearIcon} alt='clear' className='shopping-cart-btn-icon' />
            Clear
          </button>
          <button className='shopping-cart-btn' onClick={e => props.onBuyBtnClicked(e, totalItemsPrice.toFixed(2), totalItemsCount)}>
            <img src={buyIcon} alt='buy' className='shopping-cart-btn-icon' />
            Buy
            </button>
        </div>
      </div>
    )
}

class ShoppingCartContainer extends React.Component {
  onCloseBtnClicked = () => {
    this.props.toggleShoppingCartDisplay(false);
    this.props.blurPage(false);
  }

  onClearBtnClicked = () => {
    this.props.clearShoppingCart();
    this.props.toggleAddNewItemNotification(false);
  }

  onBuyBtnClicked = (e, totalItemsPrice, totalItemsCount) => {
    swal("The payment was successful!", `OK, we will assume that you have paid :)\n You spent $${totalItemsPrice} for ${totalItemsCount} product(s).`, "success");
    this.props.clearShoppingCart();
    this.props.toggleAddNewItemNotification(false);
  }

  render() {
    return <ShoppingCart {...this.props} onCloseBtnClicked={this.onCloseBtnClicked} onClearBtnClicked={this.onClearBtnClicked} onBuyBtnClicked={this.onBuyBtnClicked}
    deleteItemFromShoppingCart={this.props.deleteItemFromShoppingCart}
    toggleAddNewItemNotification={this.props.toggleAddNewItemNotification} />
  }
}

const mstp = state => {
  return {
    shoppingCartIsOpen: state.header.shoppingCart.isOpen,
    addedItems: state.menu.addedItems,
  }
}

export default connect(mstp, {
  toggleShoppingCartDisplay: toggleShoppingCartDisplayAC,
  blurPage: blurPageAC,
  clearShoppingCart: clearShoppingCartAC,
  toggleAddNewItemNotification: toggleAddNewItemNotificationAC,
  deleteItemFromShoppingCart: deleteItemFromShoppingCartAC,
})(ShoppingCartContainer);