import React, { useState } from "react";
import './Menu.css';
import PizzaMenuContainer from '../PizzaMenu/PizzaMenu';
import BurgerMenuContainer from '../BurgerMenu/BurgerMenu';
import SaladMenuContainer from '../SaladMenu/SaladMenu';
import SandwichMenuContainer from '../SandwichMenu/SandwichMenu';
import DrinksMenuContainer from "../DrinksMenu/DrinksMenu";

function Menu(props) {
    const [menu, setMenu] = useState('Pizzas');
    return (
      <div className='menu-container' id='menu'>
        <ul className='menu-toggle-list' onClick={e => props.onMenuToggleListClicked(e, setMenu)}>
          <li className='menu-toggle-list-item menu-list-item-selected'>Pizzas</li> 
          <li className='menu-toggle-list-item'>Burgers</li> 
          <li className='menu-toggle-list-item'>Salads</li> 
          <li className='menu-toggle-list-item'>Sandwiches</li> 
          <li className='menu-toggle-list-item'>Drinks</li>  
        </ul>
        <ul className='menu-list'>
          { menu === 'Pizzas' && <PizzaMenuContainer onPizzaSizeBtnClicked={props.onPizzaSizeBtnClicked} onLeftArrowClicked={props.onLeftArrowClicked} 
          onRightArrowClicked={props.onRightArrowClicked} /> }
          { menu === 'Burgers' && <BurgerMenuContainer onLeftArrowClicked={props.onLeftArrowClicked} onRightArrowClicked={props.onRightArrowClicked} /> }
          { menu === 'Salads' && <SaladMenuContainer onLeftArrowClicked={props.onLeftArrowClicked} onRightArrowClicked={props.onRightArrowClicked} /> }
          { menu === 'Sandwiches' && <SandwichMenuContainer onLeftArrowClicked={props.onLeftArrowClicked} onRightArrowClicked={props.onRightArrowClicked} /> }
          { menu === 'Drinks' && <DrinksMenuContainer onLeftArrowClicked={props.onLeftArrowClicked} onRightArrowClicked={props.onRightArrowClicked} /> }
        </ul>
      </div>
    );
}

class MenuContainer extends React.Component {
  onLeftArrowClicked(e, setNumberOfDishes) {
    return +e.target.nextElementSibling.textContent >= 1 && setNumberOfDishes(--e.target.nextElementSibling.textContent);
  }

  onRightArrowClicked(e, setNumberOfDishes) {
    return setNumberOfDishes(++e.target.previousElementSibling.textContent);
  }

  onPizzaSizeBtnClicked(e, setSize) {
    if (e.target.tagName === 'DIV' || e.target.classList.contains('selected')) return;
    Array.from(e.target.parentNode.children).forEach(btn => {
      if (btn.classList.contains('selected')) {
        btn.classList.remove('selected');
      }

      e.target.classList.add('selected');
    });

    setSize(parseInt(e.target.textContent));
  }

  onMenuToggleListClicked(e, setMenu) {
    if (e.target.tagName !== 'LI') return;
    setMenu(e.target.textContent);
    Array.from(e.target.parentNode.children).forEach(child => {
      if (child.classList.contains('menu-list-item-selected')) child.classList.remove('menu-list-item-selected');
    });
    e.target.classList.add('menu-list-item-selected');
  }

  render() {
    return <Menu onLeftArrowClicked={this.onLeftArrowClicked} onRightArrowClicked={this.onRightArrowClicked} onPizzaSizeBtnClicked={this.onPizzaSizeBtnClicked} 
    onMenuToggleListClicked={this.onMenuToggleListClicked} />
  }
}

export default MenuContainer;